import React,{useState} from 'react';
import {Drawer,List,ListItem,ListItemIcon,ListItemText,makeStyles} from '@material-ui/core';
import SettingsIcon from '@material-ui/icons/Settings';
import BookmarkIcon from '@material-ui/icons/Bookmark';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import WebAssetIcon from '@material-ui/icons/WebAsset';
import {Link,useHistory} from 'react-router-dom';
import Firebase from 'firebase';
import {auth} from '../../firebase/firebaseInit';
import Error from '../../Reusable/error/error';
import Toast from '../../Reusable/toast/toast';
import Loader from '../../Reusable/loader/loader';

const style = makeStyles((theme)=>({
	listitem:{
		color:'black',
		textDecoration:'none',
		'&:hover':{
			backgroundColor:'#f2f2f2',
			cursor:'pointer',
		}
	}
}));


export default function Sidebar(props) {
	// body...
	const classes = style();
	const history = useHistory();
	const [visiblity,setVisiblity] = useState(props.sidebaropen);
	const [loading,setLoading] = useState(false);
    const [error,setError] = useState();
    const [openDialog,setOpenDialog] = useState(false);
    const [toast,setToast] = useState(false);
    const [toastMessage,setToastMessage] = useState();
    
    const handleMyBlog=async()=>{
    	setLoading(true);
    	const user = await Firebase.auth().currentUser;
    	history.push('/MyProfileCard/user/written-blogs/'+user.displayName);
    	setLoading(false);
    }

    const handleLogout=async()=>{
    	setLoading(true)
    	await Firebase.auth().signOut().then(function() {
    	  setLoading(false)
    	  setToastMessage("Logout Successful");
		  setToast(true);
		  history.push('/MyProfileCard/');
		}).catch(function(error) {
		  setLoading(false);
		  setError(error.message);
		  setOpenDialog(true);
		});
    }

	return(
		<div>
		<Drawer
		open={visiblity}
		onClose={()=>props.setSidebaropen(false)}
		anchor="right">
		<List>
		 <Link to="/MyProfileCard/user/account/setting" className={classes.listitem}>
		 <ListItem className={classes.listitem}>
		   <ListItemIcon><SettingsIcon style={{color:'#00e600'}} fontSize="large"/></ListItemIcon>
		   <ListItemText primary="Manage Account"/>
		 </ListItem>
		 </Link>
		 <Link to="/MyProfileCard/user/saved/blogs" className={classes.listitem}>
		 <ListItem className={classes.listitem}>
		   <ListItemIcon><BookmarkIcon style={{color:'#00e600'}} fontSize="large"/></ListItemIcon>
		   <ListItemText primary="Saved Blogs"/>
		 </ListItem>
		 </Link>
		 <ListItem onClick={handleMyBlog} className={classes.listitem}>
		   <ListItemIcon><WebAssetIcon style={{color:'#00e600'}} fontSize="large"/></ListItemIcon>
		   <ListItemText primary="My Blogs"/>
		 </ListItem>
		 <ListItem onClick={handleLogout} className={classes.listitem}>
		   <ListItemIcon><ExitToAppIcon style={{color:'#00e600'}} fontSize="large"/></ListItemIcon>
		   <ListItemText primary="Logout"/>
		 </ListItem>
		</List>
		</Drawer>
		{
            loading ? <Loader/> : null
        }
        {
            openDialog ? <Error openDialog={openDialog} setOpenDialog={setOpenDialog} error={error} /> : null
        }
        {
            toast ? <Toast message={toastMessage} /> : null
        }
		</div>
		)
}