import React,{useState,useEffect} from 'react';
import {AppBar,Toolbar,makeStyles,Box,Typography,IconButton,Avatar} from '@material-ui/core';
import PersonIcon from '@material-ui/icons/Person';
import Sidebar from '../sidebar/sidebar';
import Firebase from 'firebase';
import {auth} from '../../firebase/firebaseInit';
import Loader from '../../Reusable/loader/loader';
const style=makeStyles((theme)=>({
	header:{
    backgroundImage:'linear-gradient(to right, #00e600, #00e600)',
    color:'white',
    paddingRight:'0%',
    marginLeft:'0%',
    marginRight:'0%',
    paddingLeft:'0%',
  },
  loginbox:{
    width:'100%',
    textAlign:'right',
    marginRight:'1%',
  },
  avatar:{
  	width: theme.spacing(7),
    height: theme.spacing(7),
    ['@media (max-width:600px)']:{
    	width: theme.spacing(5),
        height: theme.spacing(5),
    }
  }
}))
export default function Navbar(props){
	const classes = style();
	const [sidebaropen,setSidebaropen] = useState(false);
	
	const handleSidebar=()=>{
		setSidebaropen(true);
	}
	const [user,setUser] = useState();

	useEffect(()=>{
		Firebase.auth().onAuthStateChanged((user)=>{
			if(user){setUser(user)}
		})
	},[])
	if (user) {
		return(<div>
		<AppBar color='transparent' className={classes.header} position="static">
		<Toolbar className={classes.header}>
		    <Box letterSpacing={3} fontSize={35} style={{width:'100%',padding:"0%",margin:"0%"}} display={{xs:'none',md:'block'}}>
		      Welcome! {user.displayName}
		    </Box>
		    <Box letterSpacing={1} fontSize={25} style={{width:'100%',padding:"0%",margin:"0%"}} display={{xs:'block',md:'none'}}>
		      Welcome! {user.displayName}
		    </Box>
		   <Box className={classes.loginbox}>
		     <IconButton id="profile" onClick={handleSidebar}>
		       {
		       	user ? <Avatar src={user.photoURL} className={classes.avatar}/> : <PersonIcon fontSize="large" style={{color:'green'}}/>
		       }
		     </IconButton>
		   </Box>
		</Toolbar>
		</AppBar>
		{
			sidebaropen ? <Sidebar sidebaropen={sidebaropen} setSidebaropen={setSidebaropen}/> : null
		}
		</div>)
	}
	else{
		return(<Loader/>)
	}
	
}